import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));

// Category
const Category = React.lazy(() => import('./views/category/Category'));
const CategoryCreate = React.lazy(() => import('./views/category/Create'));

// Brand
const Brand = React.lazy(() => import('./views/brand/Brand'));
const BrandCreate = React.lazy(() => import('./views/brand/Create'));

// Brand
const Product = React.lazy(() => import('./views/product/Product'));
const ProductCreate = React.lazy(() => import('./views/product/Create'));


const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard }, 
  { path: '/category', exact: true,  name: 'Category', element: Category }, 
  { path: '/category/add', name: 'Category', element: CategoryCreate }, 
  { path: '/brands', exact: true,  name: 'Brand', element: Brand }, 
  { path: '/brands/add', name: 'Brand', element: BrandCreate },
  { path: '/products', name: 'Brand', element: Product }, 
  { path: '/products/add', name: 'Brand', element: ProductCreate }, 

]

export default routes
