import { CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';
import React, { useState } from 'react';

const DataTable = ({ data, headers }) => {
//   console.log(data)
  const [sortedData, setSortedData] = useState(data);
  const [searchTerm, setSearchTerm] = useState('');
  console.log(sortedData, )

  const sortData = (key) => {
    const sorted = sortedData.sort((a, b) =>
      a[key].localeCompare(b[key])
    );
    setSortedData(sorted);
  };

  const searchFilter = (e) => {
    setSearchTerm(e.target.value);
  };
  const filteredData = data;

console.log(filteredData, sortedData)

  return (
    <div>
      <input
        type="text"
        placeholder="Search by name"
        value={searchTerm}
        onChange={searchFilter}
      />
      <CTable border={1} striped>
      <CTableHead>
        <CTableRow>
           
            {headers.map((header,  key) => (
              <CTableHeaderCell width={header.width} key={key}  onClick={header.short ? () => sortData(header.field) : null}  className={`${header.shortby?header.shortby:''} ${header.short ? 'shortable' : ''}`}>
                {header.name}
              </CTableHeaderCell>
            ))}
          </CTableRow>
        </CTableHead>
        <CTableBody>
          {filteredData.map((item) => (
            <CTableRow key={item._id}>               
                {headers.map((header,j) => ( 
                    <CTableDataCell key={j}>
                        {header.type ==="image" && item[header.field] ?<img src={'http://localhost:5000/'+item[header.field]}  width={100}/>:item[header.field]}
                    </CTableDataCell>   
                ))}
            </CTableRow>
          ))}
        </CTableBody>
        </CTable>
    </div>
  );
};

export default DataTable;
